@import "~antd/dist/antd.css";

body {
  margin: 0;
  font-family: "IBM Plex Sans", sans-serif;
  background-color: #f0f2f5;
}

.logo-container {
  align-items: center;
  display: flex;
  justify-content: center;
  margin-top: 20px;
  max-width: 200px;
}

.logo-container img {
  transition: width 0.5;
}

.logo-large {
  width: 130px;
}

.logo-small {
  width: 40px;
  height: 40px;
}

.site-layout-sub-header-background {
  background: #fff;
}

.site-layout-background {
  background: #fff;
}

.ant-radio-group label {
  margin: 5px;
}

.pointer {
  cursor: pointer;
}

.d-flex-row {
  display: flex;
  flex-direction: row;
}

.d-flex-col {
  display: flex;
  flex-direction: column;
}

.margin-zero {
  margin: 0;
}

.ant-checkbox-wrapper {
  margin: 0 !important;
  padding: 5px;
  border-radius: 5px;
}
.ant-checkbox-wrapper:hover {
  background-color: #e6fffb;
}

.ant-tag:focus {
  color: #ffffff;
  background-color: rgb(0, 0, 0);
  border-color: rgb(0, 0, 0);
}

.ant-btn-primary {
  background-color: rgb(0, 0, 0);
  border-color: rgb(0, 0, 0);
}

.ant-btn-primary:hover {
  background-color: rgb(0, 0, 0);
  border-color: rgb(0, 0, 0);
}

.ant-btn-primary:focus {
  background-color: rgb(0, 0, 0);
  border-color: rgb(0, 0, 0);
}

.ant-table-cell {
  word-wrap: break-word;
}

.ant-image-img {
  padding: 20px !important;
}

.grid-responsive {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  padding: 10px;
  grid-gap: 10px;
}

@media (min-width: 768px) {
  .grid-responsive {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1000px) {
  .grid-responsive {
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (min-width: 1200px) {
  .grid-responsive {
    grid-template-columns: repeat(5, 1fr);
  }
}
@media (min-width: 1400px) {
  .grid-responsive {
    grid-template-columns: repeat(6, 1fr);
  }
}

.on-hover:hover {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: box-shadow 0.6s;
}

.button-green {
  background-color: white;
  border-color: #95de64;
  color: #73d13d;
  font-weight: bold;
}

.button-green:hover {
  background-color: #95de64;
  border-color: #73d13d;
  color: white;
}

.button-red {
  background-color: white;
  border-color: #ff4d4f;
  color: #ff4d4f;
  font-weight: bold;
}

.button-red:hover {
  background-color: #f5222d;
  border-color: #ffa39e;
  color: white;
}

.button-blue {
  background-color: white;
  border-color: #bae7ff;
  color: #40a9ff;
  font-weight: bold;
}

.button-blue:hover {
  background-color: #1890ff;
  border-color: #40a9ff;
  color: white;
}
.button-orange {
  background-color: white;
  border-color: #ffc069;
  color: #fa8c16;
  font-weight: bold;
}

.button-orange:hover {
  background-color: #d46b08;
  border-color: #ad4e00;
  color: white;
}

.menu-button {
  min-width: 60px;
  height: 40px;
  color: #fff;
  padding: 5px 10px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  display: inline-block;
  outline: none;
  border-radius: 5px;
  border: none;
  background: #343a40;
  box-shadow: 0 5px #212529;
}
.menu-button:hover {
  box-shadow: 0 3px #212529;
}
.menu-button:active {
  box-shadow: 0 0 #212529;
}

.collection-list {
  padding: 10px;
  overflow: auto;
}

.collection-list .ant-list-items {
  display: flex;
}

@media only screen and (min-width: 768px) {
  .collection-list .ant-list-items {
    display: flex;
    flex-direction: column;
  }
}
